import React from 'react';
import logo from '../../assets/img/logo.svg';
import './Popup.css';

class Popup extends React.Component {

  constructor(props) {
    super(props);

    this.videoDataGathered = false;

    var that = this;

    chrome.runtime.onMessage.addListener(
        function(message, sender, sendResponse) {
            switch(message.type) {
                case "urlUpdated":
                    this.getVideoData(); // When url updates, update video data
                    break;
                default:
                    console.error("Unrecognised message: ", message);
            }
        }.bind(that)
    );

    // Bind this for various functions
    this.getVideoData = this.getVideoData.bind(this);
    this.startLoop = this.startLoop.bind(this);
    this.pauseLoop = this.pauseLoop.bind(this);
    this.resumeLoop = this.resumeLoop.bind(this);
    this.stopLoop = this.stopLoop.bind(this);

    this.onStartTimeChange = this.onStartTimeChange.bind(this);
    this.onEndTimeChange = this.onEndTimeChange.bind(this);
    this.onSpeedChange = this.onSpeedChange.bind(this);

    // Add listener to get video data when things load
    window.addEventListener('load', this.getVideoData);

    // Create refs used to get html input values
    this.startTime = React.createRef();
    this.endTime = React.createRef();
    this.speed = React.createRef();

    if (localStorage.getItem("popup_state")) {
      this.loadState();
    } else {
      this.state = {
        duration: "0.0",
        pageHasVideo: false,
        paused: false,
        videoDataGathered: false,
        startTime: "00:00",
        endTime: "00:00",
        speed: "1.0",
      };
    }
    window.addEventListener('beforeunload', function (e) {
                                    e.preventDefault();
                                    this.saveState();
                                  }.bind(this));
  }

  loadState() {
    this.setState(JSON.parse(localStorage.getItem('popup_state')))
  }
  saveState() {
    localStorage.setItem('popup_state', JSON.stringify(this.state));
  }

  getVideoData() {
    var that = this;
    chrome.tabs.query({active: true, currentWindow: true}, (tabs) => {
        chrome.tabs.sendMessage(tabs[0].id, {type: "getVideoData"}, (data) => {
          if(typeof data == "undefined") {
              // That's kind of bad
              if(chrome.runtime.lastError) {
                  // We couldn't talk to the content script, probably it's not there
                  console.log("Content script not found!!!");
              }
          } else {
              // set state
              that.setState({duration: data.duration, pageHasVideo: data.pageHasVideo, videoDataGathered: true});
          }
        });
    });
  }

  minToSec(mins) {
    // source: https://stackoverflow.com/questions/8907764/convert-minutes-to-seconds
    var parts = mins.toString().split(':'),
        minutes = +parts[0],
        seconds = +parts[1];
    return (minutes * 60 + seconds).toFixed(3);
  }

  secToMin(sec) {
    // code adapted from https://www.codegrepper.com/code-examples/javascript/convert+minutes+into+seconds+javascript

    sec = parseInt(sec, 10); // convert value to number if it's string
    let hours   = Math.floor(sec / 3600); // get hours
    let minutes = Math.floor((sec - (hours * 3600)) / 60); // get minutes
    let seconds = sec - (hours * 3600) - (minutes * 60); //  get seconds
    // add 0 if value < 10; Example: 2 => 02
    if (hours   < 10) {hours   = "0"+hours;}
    if (minutes < 10) {minutes = "0"+minutes;}
    if (seconds < 10) {seconds = "0"+seconds;}

    if (hours == 0) {
      return minutes+':'+seconds;
    } else {
      return hours+':'+minutes+':'+seconds; // Return is HH : MM : SS
    }

  }

  startLoop(e) {
    e.preventDefault();
    if (this.state.paused) {
      this.resumeLoop();
    } else {
      var start = this.minToSec(this.startTime.current.value)
      var end = this.minToSec(this.endTime.current.value)
      console.log(this.speed.current.value)

      chrome.tabs.query({active: true, currentWindow: true}, (tabs) => {
          chrome.tabs.sendMessage(tabs[0].id, {
            type: "startLoop",
            startTime: start.toString(),
            endTime: end.toString(),
            speed: this.speed.current.value.toString()
          });
      });
      // chrome.tabs.query({active: true, currentWindow: true}, (tabs) => {
      //     chrome.tabs.sendMessage(tabs[0].id, {
      //       type: "startLoop",
      //       startTime: "60",
      //       endTime: "90",
      //       speed: "3.0"
      //     });
      // });
    }
  }

  pauseLoop(e) {
    e.preventDefault();
    this.setState({paused: true});
    chrome.tabs.query({active: true, currentWindow: true}, (tabs) => {
        chrome.tabs.sendMessage(tabs[0].id, {type: "pauseLoop"})
    });
  }

  resumeLoop() {
    this.setState({paused: false});
    chrome.tabs.query({active: true, currentWindow: true}, (tabs) => {
        chrome.tabs.sendMessage(tabs[0].id, {type: "resumeLoop"})
    });
  }

  stopLoop(e) {
    e.preventDefault();
    this.setState({paused: false});
    chrome.tabs.query({active: true, currentWindow: true}, (tabs) => {
        chrome.tabs.sendMessage(tabs[0].id, {type: "stopLoop"})
    });
  }

  onStartTimeChange() {
    this.setState({startTime: this.startTime.current.value});
  }

  onEndTimeChange() {
    this.setState({endTime: this.endTime.current.value});
  }

  onSpeedChange() {
    this.setState({speed: this.speed.current.value});
  }

  Main() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>
            Does the page you're on have a video? {this.state.pageHasVideo.toString()}. Also, the duration for the video is {this.secToMin(this.state.duration).toString()}.
          </p>
          <div>
            <label for="startTime">Start time:</label>
            <input type="text" id="startTime" name="start" onChange={this.onStartTimeChange} ref={this.startTime} defaultValue="00:00"></input><br></br>
            <label for="endTime">End time:</label>
            <input type="text" id="endTime" name="end" onChange={this.onEndTimeChange} ref={this.endTime} defaultValue="00:00"></input><br></br>
            <label for="speed">Speed:</label>
            <input type="text" id="speed" name="speed" onChange={this.onSpeedChange} ref={this.speed} defaultValue="1.0"></input><br></br>
            <button onClick={this.startLoop}>Play</button>
            <button onClick={this.pauseLoop}>Pause</button>
            <button onClick={this.stopLoop}>Stop</button>
          </div>
        </header>
      </div>
    );
  }

  Loading() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>
            Loading video data...
          </p>
        </header>
      </div>
    );
  }

  NoVideo() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>
            Video not found on current page. Use Loopro on a different page, or try refreshing.
          </p>
        </header>
      </div>
    );
  }

  render() {
    if (this.state.pageHasVideo) {
      if (this.state.videoDataGathered) {
        return this.Main();
      } else {
        return this.Loading();
      }
    } else {
      return this.NoVideo();
    }
  }
}



export default Popup
